
import Vue, {PropType} from "vue";

type TCard = {
  title : string;
  ul : {
    id: number,
    text: string;
  }[]
}

export default Vue.extend({
  name: "MobileAccordion",
  props: {
    card: Object as PropType<TCard>
  },
  data: function () {
    return {
      open: false,
    }
  },
  methods: {
    toggle() {
      this.open =!this.open
    }
  },
})
