import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    windowWidth:Number
  },
  getters: {
    windowWidth: state => {
      return state.windowWidth
    }
  },
  mutations: {
    setWindowWidth: (state, payload) =>{
      state.windowWidth = payload
    }
  },
  actions: {},
  modules: {},
});
