import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import MainView from "@/views/MainView.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "",
    name: "Главная",
    component: MainView,
  },

];

const router = new VueRouter({
  mode: "history",
  base: "gigdata",
  routes,

});

export default router;
